.controlPanelBG {
  background-color: rgb(51, 51, 51);
  background-image: url("../GFX/world.svg");
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  padding: 15%;
  padding-top: 5%;
}

#controlPanelHeader {
  color: white;
}

#panelBox {
  width: 100%;
  height: auto;

  /* border: 1px solid red; */

  padding: 2%;
}

#sliderBox {
  float: left;
  /* border: 1px solid green; */
  width: 70%;
  min-height: 600px;
}

#leftSliderBox {
  float: left;
  margin-left: 10%;
}

#rightSliderBox {
  margin-right: 10%;
  float: right;
}

.sliderUnit {
  padding-top: 50px;
  padding-bottom: 25px;
}

h3.sliderHeader {
  color: white;
  font-size: 14px;
}

.sliderLabel {
  float: left;
  min-width: 60px;
  padding-top: 12px;
  text-align: right;

  color: rgb(100, 100, 100);
  font-size: 12px;
}

.leftAlign {
  text-align: left;
}

.slider {
  float: left;
  margin-left: 5px;
  margin-right: 5px;
}

#logBox {
  float: right;
  /* border: 1px solid blue; */
  width: 25%;
  min-height: 600px;
  max-height: 600px;

  background-color: rgb(60, 60, 60);
  border-radius: 10px;
  padding: 3px;
  color: rgb(100, 100, 100);
  font-size: 12px;

  overflow: scroll;
}

#logBox::-webkit-scrollbar {
  display: none;
}

#applyButton {
  float: right;
  background-color: lightblue;
  border-radius: 5px;
  border: none;

  margin-top: 10px;
  margin-right: 2%;
}

.fadeOutApplied {
  visibility: hidden;
  opacity: 0;

  animation-name: fadeOut;
  animation-delay: 0s;
  animation-duration: 5s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
}
