body {
  overflow-x: hidden;
}

h1 {
  font-family: "Roboto", sans-serif;
}

p {
  font-family: "Open Sans", sans-serif;
}

@media screen and (max-width: 600px) {
  #quote {
    font-family: "Roboto", sans-serif;
    font-size: 3.5vw;
  }
  #textPic1 {
    font-family: "Roboto", sans-serif;
    font-size: 2.5vw;
  }
}

@media screen and (min-width: 601px) {
  #quote {
    font-family: "Roboto", sans-serif;

    font-size: 2vw;
  }
  #textPic1 {
    font-family: "Roboto", sans-serif;

    font-size: 1.5vw;
  }
}

.blueText {
  color: rgb(66, 133, 244);
}
.redText {
  color: rgb(234, 67, 53);
}
.yellowText {
  color: rgb(251, 188, 5);
}
.greenText {
  color: rgb(52, 168, 83);
}

@keyframes fadeOut {
  0% {
    visibility: visible;
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes fadeIn {
  0% {
    visibility: hidden;
    opacity: 0;
  }
  20% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

#introBox {
  z-index: 100;

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(67, 67, 67);
}

.animate {
  visibility: hidden;
  opacity: 0;

  animation-name: fadeOut;
  animation-delay: 0s;
  animation-duration: 5s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
}

#introTextBox {
  width: 50%;
  margin: 0 auto;
  text-align: center;
  font-style: italic;
  color: lightgrey;
}

div#endBox #introTextBox {
  color: grey;
}

#logoBox {
  width: 500px;
  height: 281px;
  margin: 0 auto;
  margin-top: 20%;
  padding: 0;
}

.invisible {
  visibility: hidden;
}

#endBox {
  max-width: 400px;
  margin: 25% auto;

  text-align: center;

  visibility: visible;
  opacity: 1;

  animation-name: fadeIn;
  animation-delay: 0s;
  animation-duration: 5s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
}

/* branches page */
#branchesContainer {
  padding-top: 5vw;
}
#ourTechnologies {
  text-align: center;
  margin-left: 20vw;
  margin-right: 20vw;
  margin-top: 5vw;
  margin-bottom: 5vw;
}
#ourTechnologiesTitle {
  color: grey;
}

/* quantum page */
#quantumTitle {
  color: dimgray;
  font-size: 500%;
  margin-top: 8vw;
  margin-bottom: 8vw;
  margin-left: 1vw;
  margin-right: 1vw;
  position: relative;
  float: center;
  text-align: center;
}
#quantumText {
  font-size: 150%;
}

/* founder page */

.founderImg {
  width: 30%;
  margin-top: 2vw;
}
#founderContainer {
  float: center;
}
#founderQuote {
  margin-top: 2vw;
  margin-bottom: 2vw;
  margin-left: 21vw;
  margin-right: 21vw;
  position: relative;
  float: center;
  text-align: center;
  font-size: small;
}

.founderText {
  margin-bottom: 2vw;
}