.lt-grid-container {
  display: grid;
  grid-template-columns: 0.6fr 3.1fr 0.6fr;
  grid-template-rows: 0.5fr 0.5fr 2fr 0.6fr 1.4fr;
  gap: 1px 1px;
  grid-template-areas: ". . ." ". lt-txtPlace ." ". lt-imgPlace ." ". . ." ". . .";
}

.lt-imgPlace {
  margin-top: 5%;
  grid-area: lt-imgPlace;
  overflow: auto;
  white-space: nowrap;
}

.lt-txtPlace {
  grid-area: lt-txtPlace;
}

